import React from 'react';
import './css/KMap.css';
import './css/drawGroup.css';
import $ from 'jquery';
import Map from './Map.js';
import TruthTable from './truthTable.js';
import OptionButton from './optionButton.js';
import LogicCircuitGo from './LogicCircuitGo';


export default class KarnaughMap extends React.Component {
    constructor(props) {
      super(props);
  
      let t = 4;
      let a = this.getMatrixSquare(t);
      let p = this.getMatrixPerm(t);
      let m = this.setCoord(a, p, t);
      let s = "SOP";
      this.state = {
        squares: m,   // matice hodnot mapy
        typeMap: t,   // typ mapy, kter se m geelementsInRowovat
        perm: p,      // matice permutac
        typeSol: s,    //SOP nebo POS
        logicExpression: "", // nov state
      };
    }
  
    // metoda, kter vrac matici binrnch hodnot na zklad zadan velikosti
    getMatrixPerm(dim) {
      let col = dim;
      let row = Math.pow(2, dim);  // poet dk je dn mocninou dvou podle zadan dimenze
      let a = [];                   // pomocn matice
      for (let i = 0; i < row; i++) {   // vytvoen matice (vloen sloupc pro kad dek)
        let temp = [];
        for (let j = 0; j < col; j++)
          temp[j] = 0;
        a[i] = temp;
      }
  
      for (let j = 0; j < col; j++) {       
        let count = (Math.pow(2, dim)) / 2; // poet po sob jdoucch 0 nebo 1, kter se budou opakovat
  
        for (let i = 0; i < row; i++) {     // iterace pes dky (kombinace promnnch)
          let val = (i % (count * 2) < count) ? 0 : 1;  //stdn 0 a 1 podle vzoru
          a[i][j] = "" + val;                          // uloen jako etzec
        }
        dim--;                        // snen dimenze, aby se v dalm sloupci stdn zkrtilo
      }
      return a;
    }
  
    getMatrixSquare(dim) {      // vytvo matici, kter tvo mapu
      let row = dim;
      let col = dim;
      let deep = 2;             // jedn se o trojrozmrnou matici, kde [.][.][0] obsahuje hodnotu mapy, [.][.][1] a [.][.][2] souadnice
                                // sloupce a dku
      if (dim === 3) {          // kontrola pro ppad s 3 promnnmi
        row = 2;
        col = 4;
      }
  
      let a = [];                   // nastaven matice vloenm vektor jeden do druhho
      for (let i = 0; i < row; i++) {
        let temp = [];
        for (let j = 0; j < col; j++) {
          let t = [];
          for (let k = 0; k < deep; k++)
            t[k] = 0;
          temp[j] = t;
        }
        a[i] = temp;
      }
      return a;
    }
  
    setMatrixSquare(val) {                // metoda, kter nastav hodnotu matice na 1, 0 nebo X
      const squares = this.state.squares;
      const typeMap = this.state.typeMap;
      let r = typeMap;
      let c = typeMap;
      if (typeMap === 3) {
        r = 2;
        c = 4;
      }
      for (let i = 0; i < r; i++)
        for (let j = 0; j < c; j++) {
          squares[i][j][0] = val;
        }
      
      this.reset();
      this.setState({
        squares: squares,
      });
    }
  
    reset(){                              // resetovac metoda, kter znovu nastav vchoz nastaven pro nov vpoet
      const typeMap = this.state.typeMap;
      let r = typeMap;
      let c = typeMap;
      if (typeMap === 3) {
        r = 2;
        c = 4;
      }
      $("#elabora").prop("disabled", false);  // znovu povol tlatko (nastav ho jako aktivn)
  
      for (let i = 0; i < r; i++)             // odstran vykreslen skupin
        for (let j = 0; j < c; j++) {
          $("#" + i + j).removeClass();
          $("#" + i + j).html("");
          for (let k = 0; k < 10; k++)
            $("#" + i + j + k).remove();          
        }
      
        $("#sol").html("");                 // odstran een
        $("#cost").html("");
        $(".Solution").hide();
        $(".Solution").css("left","720px");
    }
  
    setCoord(squares, perm, typeMap) { // kd, kter nastavuje souadnice viditeln nahoe a na boku mapy
      let r = typeMap;            // squares[i][j][0] = prvek zobrazen v matici
      let c = typeMap;            // squares[i][j][1] = souadnice sloupce
      // squares[i][j][2] = souadnice dku
      if (typeMap === 3) {         // kontrola pro mapu se 3 promnnmi
        c = 4;
        r = 2;
      }
      for (let i = 0; i < c; i++) {   // prvn for cyklus prochzejc sloupce
        let l;
        if (i === 2) l = 3;          // provd potebn zmny (kvli Grayovu kdu)
        else if (i === 3) l = 2;
        else l = i;
  
        for (let j = 0; j < r; j++) { // vnoen for cyklus prochzejc dky
          let k;
          if (j % r === 2) k = 3;       // provd potebn zmny (kvli Grayovu kdu)
          else if (j % r === 3) k = 2;
          else k = j;
          // zatek nastaven souadnic
          let val = "";
          let t = typeMap;
  
          let p = 0;
          // metoda pro rozdlen na dv buky  zvl souadnice sloupce a dku
          do {
            val += perm[i * r + j][p];    // i*r+j  vzorec, kter umouje prochzet pole jako by to byla matice s r dky a c sloupci
            p++;
          } while (p < t / 2);
          squares[k][l][1] = val;   // nastaven souadnice sloupce
          val = "";
          p = Math.floor(t / 2);
          if (typeMap === 3) {        // ppad matice pro 3 promnn
            t = 2;
            p = Math.floor(t / 2 + 1);
          }
          do {
            val += perm[i * r + j][p];
            p++;
          } while (p < t);
          squares[k][l][2] = val; // nastaven souadnice dku
          //console.log("vett: "+(i*r+j)+" col: "+l+"  rig: "+k); 
        }
      }
      // console.log(squares);
      return squares;
    }
  
    setTypeMap(val) {                     //nastav typ mapy a vechny souvisejc hodnoty, aby bylo mon provst ist vpoet
      let a = this.getMatrixSquare(val);
      let b = this.getMatrixPerm(val);
      let c = this.setCoord(a, b, val);
      this.reset();
      this.setState({
        typeMap: val,
        squares: c,
        perm: b,
      });
    }
  
    setTypeSol(type){   //nastav poadovan typ een (SOP nebo POS)
      this.reset();
      this.setState({
        typeSol: type
      })
    }
  
    handleClick(i, j) { // Je vyvolna, kdy se klikne na tverec, a zmn jeho obsah (1, 0, X)
      const squares = this.state.squares; // Ulom hodnoty, kter jsou v "squares", protoe je nemu mnit pmo
      if (squares[i][j][0] === 'X')
        squares[i][j][0] = 0;
  
      else if (squares[i][j][0] === 0)
        squares[i][j][0] = 1;
      else
        squares[i][j][0] = 'X';

      this.reset();
      this.setState({ // Po proveden poadovan pravy aktualizuji stav (state)
        squares: squares,
      });
    }
  
    
    Algorithm(squares) {
  
      $("#elabora").prop("disabled", true);
      var dimCol, dimRow;
      const typeSol = this.state.typeSol;
      let val = (typeSol === "SOP")? 1 : 0 ;
  
      if (this.state.typeMap === 4) {
        dimCol = 4;
        dimRow = 4;
      }
      else
        if (this.state.typeMap === 3) {
          dimCol = 4;
          dimRow = 2;
        }
        else {
          dimCol = 2;
          dimRow = 2;
        }
  
      var groups = new Array(dimRow); // vytvom dky
  
      for (let i = 0; i < dimRow; i++) {
        groups[i] = new Array(dimCol); // vytvom sloupce
  
        for (let j = 0; j < dimCol; j++)
          groups[i][j] = []; // pro kadou buku vytvom pole
      }
  
      var index = 0; // pro uren jednotlivch (doasnch) skupin
      for (let i = 0; i < dimRow; i++) {
        for (let j = 0; j < dimCol; j++) {
  
          var count = 0; // pot, kolik prvk bylo nalezeno (doasn), slou ke kontrole, zda jde o mocninu dvojky
  
          if (squares[i][j][0] === val || (squares[i][j][0] === 'X' && this.hasOneNeighbour(squares, i, j, val))) { // pokud je buka vybrna (nap. 1 nebo X se sousedem)
            // hodnoty i a j zstvaj nezmnn
            var TempI = i;
            var TempJ = j;
  
            if (j === dimCol - 1)// nachzm se v poslednm sloupci, provedu kontrolu hran
            {
              let ok = true;
              let count2 = 0;
  
              for (let height = i; height < dimRow && ok; height++)
                if ((squares[height][dimCol - 1][0] === val || squares[height][dimCol - 1][0] === 'X') &&
                    (squares[height][0][0] === val || squares[height][0][0] === 'X')) {
                    groups[height][0].push(index);
                    groups[height][dimCol - 1].push(index);
                    count2++;
                }
                else
                  ok = false;
  
              if (count2 > 0) {
                index++;
  
                if (!isPower(2, count2)) {
                  groups[i + count2 - 1][0].pop();
                  groups[i + count2 - 1][dimCol - 1].pop();
                } 
              }
  
            }
  
            if (i === dimRow - 1)// nachzm se v poslednm dku, provdm kontrolu okraj
            {
              let ok = true;
              let count2 = 0;
  
              for (let column = j; column < dimCol && ok; column++)
                if ((squares[dimRow - 1][column][0] === val || squares[dimRow - 1][column][0] === 'X') &&
                  (squares[0][column][0] === val || squares[0][column][0] === 'X')) {
                  groups[dimRow - 1][column].push(index);
                  groups[0][column].push(index);
                  count2++;
                }
                else
                  ok = false;
  
              if (count2 > 0) {
                index++;
  
                if (!isPower(2, count2)) {
                  groups[dimRow - 1][j + count2 - 1].pop();
                  groups[0][j + count2 - 1].pop();
                }
              }
  
            }
  
            do { // Kontrola horizontlnch bunk sousedcch s danou
              groups[TempI][TempJ].push(index); // Oznam, e jsem nael skupinu propojenou s vybranm prvkem
              count++;
              TempJ++;
            } while (TempJ < dimCol && (squares[TempI][TempJ][0] === val || squares[TempI][TempJ][0] === 'X'));
            
  
            if (!isPower(2, count)) // je count mocninou sla 2?
            {
              groups[TempI][TempJ - 1].pop(); // odstranm posledn vloen prvek
              count--;
            }
  
            var CountVer;
            var depth = 100; // udv, kolik dk ve sloupci prvku je platnch
            var isOk = true; // slou ke kontrole, zda nedochz k peruen ve sloupci
            for (let offset = 0; offset < count; offset++) { // pro kad sloupec
              TempI = i + 1;
              TempJ = j + offset;
              CountVer = 1;
  
              while (TempI < dimRow && CountVer < depth) {
                if (!(squares[TempI][TempJ][0] === val || squares[TempI][TempJ][0] === 'X')) {
                  if (offset !== 0 && CountVer !== depth) { //umouje "oznait" skuten pouiteln skupiny
  
                    var rig = TempI;
                    if (!isPower(2, offset))// v ppad matice 4x4 pouze kdy spo=3
                    {

  
                      if (!isPower(2, CountVer)) // je teba znt vku vi vybranmu prvku
                        rig--;
  
                      groups[TempI][TempJ].push(index); // vyhne se kontrole v dalch smykch  nelze odstranit nco, co jet nebylo vloeno
  
                      if (TempI >= depth) // podle toho, kde se nachzm, pouiji vku skupiny (depth) nebo aktuln vku
                        depth = TempI;
                      else
                        depth--;
  
                      for (; rig <= depth; rig++)
                        for (let col = TempJ - 1; col <= offset; col++)
                          groups[rig][col].pop();
  
                      isOk = false; // pro nsledujc kontrolu (ni rovn)
                    }
                  }
                  break;
                }
                groups[TempI][TempJ].push(index);
                TempI++;
                CountVer++;
              }
  
              if (CountVer < depth)
                depth = CountVer;
  
              if (!isPower(2, CountVer) && isOk) { // protoe jsem ji "vyistil" (odstranil) pi nastaven isOk na false, nemusm to dlat znovu
                groups[TempI - 1][TempJ].pop();
                depth--;
              }
            }
            index++;
          }
        }
  
      }
      if (
        (squares[0][dimCol - 2][0] === val || squares[0][dimCol - 2][0] === 'X') &&
        (squares[0][dimCol - 1][0] === val || squares[0][dimCol - 1][0] === 'X') &&
        (squares[dimRow - 1][dimCol - 2][0] === val || squares[dimRow - 1][dimCol - 2][0] === 'X') &&
        (squares[dimRow - 1][dimCol - 1][0] === val || squares[dimRow - 1][dimCol - 1][0] === 'X')
      ) {
        groups[0][dimCol - 2].push(index);
        groups[0][dimCol - 1].push(index);
        groups[dimRow - 1][dimCol - 2].push(index);
        groups[dimRow - 1][dimCol - 1].push(index);
        index++;
      }
      console.log("Algorithm:");
      console.log(groups);
      this.GroupUp(squares, $.extend(true, [], groups));
      
    }
    hasOneNeighbour(squares, i, j, val) {
      const dimRow = squares.length;
      const dimCol = squares[0].length;
    
      const dirs = [
        [0, 1],
        [1, 0],
        [0, -1],
        [-1, 0]
      ];
    
      for (let [dx, dy] of dirs) {
        const ni = (i + dx + dimRow) % dimRow;
        const nj = (j + dy + dimCol) % dimCol;
        const neighbor = squares[ni][nj][0];
        if (neighbor === val || neighbor === 'X') {
          return true;
        }
      }
      return false;
    }
  
    GroupUp(squares, values) {
      var groups = [];
  
      var group1 = [];
      var group2 = [];
      var obj1, obj2;
      var dimCol, dimRow;
      const typeSol = this.state.typeSol;
      let val = (typeSol === "SOP")? 1 : 0 ;
  
      if (this.state.typeMap === 4) {
        dimCol = 4;
        dimRow = 4;
      }
      else
        if (this.state.typeMap === 3) {
          dimCol = 4;
          dimRow = 2;
        }
        else {
          dimCol = 2;
          dimRow = 2;
        }
  
        if (
          (squares[0][0][0] === val || squares[0][0][0] === 'X') &&
          (squares[0][dimCol - 1][0] === val || squares[0][dimCol - 1][0] === 'X') &&
          (squares[dimRow - 1][0][0] === val || squares[dimRow - 1][0][0] === 'X') &&
          (squares[dimRow - 1][dimCol - 1][0] === val || squares[dimRow - 1][dimCol - 1][0] === 'X')
        )
      {
  
        obj1 = {
          row: 0,
          col: 0
        };
  
        group1.push(obj1);
        
        obj1 = {
          row: 0,
          col: dimCol-1
        };
  
        group1.push(obj1);
        
        obj1 = {
          row: dimRow-1,
          col: 0
        };
  
        group1.push(obj1);
  
        obj1 = {
          row: dimRow-1,
          col: dimCol-1
        };
  
        group1.push(obj1);
  
        groups.push(group1);
  
        if (group1.some(cell => squares[cell.row][cell.col][0] === val)) {
          groups.push(group1);
        }
        group1 = [];
       
      }
  
      for (let i = 0; i < dimRow; i++) {
        for (let j = 0; j < dimCol; j++) {
  
          if (squares[i][j][0] === val || (squares[i][j][0] === 'X' && this.hasOneNeighbour(squares, i, j, val))) { // squares[i][j] je vybran prvek
  
            var index = values[i][j][0];
            var startRow = i;
            var startCol = j;
  
            if (j === dimCol - 1) {
              while (startRow < dimRow && values[startRow][j][0] === index && values[startRow][0][0] === index) {
  
                obj1 = {
                  row: startRow,
                  col: 0
                };
  
                obj2 = {
                  row: startRow,
                  col: j
                };
  
                values[startRow][j].shift();
                values[startRow][0].shift();
  
                group1.push(obj1);
                group1.push(obj2);
  
                startRow++;
              }
  
              if (group1.length > 0) {
                if (group1.some(cell => squares[cell.row][cell.col][0] === val)) {
                  groups.push(group1);
                }
                group1 = [];
                index = values[i][j][0];
              }
  
  
              startRow = i;
              startCol = j;
  
            }
  
            if (i === dimRow - 1) {
              while (startCol < dimCol && values[i][startCol][0] === index && values[0][startCol][0] === index) {
  
                obj1 = {
                  row: i,
                  col: startCol
                };
  
                obj2 = {
                  row: 0,
                  col: startCol
                };
  
                values[0][startCol].shift();
                values[i][startCol].shift();
  
                group1.push(obj1);
                group1.push(obj2);
  
                startCol++;
              }
  
              if (group1.length > 0) {
                group1.sort(function (a, b) { return a.row - b.row }); // provedu seazen podle velikosti
                if (group1.some(cell => squares[cell.row][cell.col][0] === val)) {
                  groups.push(group1);
                }
                
                group1 = [];
                index = values[i][j][0];
              }
  
  
              startRow = i;
              startCol = j;
            }
  
            while (startCol < dimCol && values[startRow][startCol][0] === index)
              startCol++;
  
            while (startRow < dimRow && values[startRow][startCol - 1][0] === index)
              startRow++;
  
  
            for (let EleInRow = i; EleInRow < startRow; EleInRow++)
              for (let FineCol = j; FineCol < startCol; FineCol++) {
                obj1 = {
                  row: EleInRow,
                  col: FineCol
                };
                group1.push(obj1);
              }
  
              if (group1.some(cell => squares[cell.row][cell.col][0] === val)) {
                groups.push(group1);
              }
              
  
            startRow = i;
            startCol = j;
  
            while (startRow < dimRow && values[startRow][startCol][0] === index)
              startRow++;
  
            while (startCol < dimCol && values[startRow - 1][startCol][0] === index)
              startCol++;
  
            for (let EleInRow = i; EleInRow < startRow; EleInRow++)
              for (let FineCol = j; FineCol < startCol; FineCol++) {
                obj1 = {
                  row: EleInRow,
                  col: FineCol
                };
                group2.push(obj1);
              }
  
            var equal = true;
            if (group1.length === group2.length)
            {
              for (let v = 0; v < group1.length && equal; v++)
                if (group1[v].row !== group2[v].row && group1[v].col !== group2[v].col)
                  equal = false;
            }
                  else
                  if (group2.some(cell => squares[cell.row][cell.col][0] === val)) {
                    groups.push(group2);
                  }
                  
  
            if (!equal)
              if (group2.some(cell => squares[cell.row][cell.col][0] === val)) {
                groups.push(group2);
              }
              
  
            group1 = [];
            group2 = [];
  
            for (let k = 0; k < dimRow; k++)
              for (let z = 0; z < dimCol; z++)
                if (values[k][z][0] === index)
                  values[k][z].shift();
  
          }
  
        }
      }
      console.log("GroupUp:");
      console.log(groups);
      this.CleanAlgorithm($.extend(true, [], groups));
    }
    
    CleanAlgorithm(groups) {
      groups.sort((a, b) => b.length - a.length); // seadit sestupn dle velikosti
      const temp = $.extend(true, [], groups); // deep copy
    
      for (let i = 0; i < temp.length; i++) {
        for (let j = i + 1; j < temp.length; j++) {
          for (let k = 0; k < temp[i].length; k++) {
            for (let l = 0; l < temp[j].length; l++) {
              if (
                temp[i][k].row === temp[j][l].row &&
                temp[i][k].col === temp[j][l].col
              ) {
                temp[j].splice(l, 1);
                l--;
              }
            }
          }
        }
      }
    
      // Eliminace pln pokrytch skupin
      for (let v = 0; v < temp.length; v++) {
        let eliminated = true;
        for (let i = 0; i < temp[v].length; i++) {
          const cell = temp[v][i];
          let found = false;
          for (let k = 0; k < temp.length; k++) {
            if (v === k) continue;
            if (
              temp[k].some(
                other =>
                  other.row === cell.row && other.col === cell.col
              )
            ) {
              found = true;
              break;
            }
          }
          if (!found) {
            eliminated = false;
            break;
          }
        }
        if (eliminated) temp[v] = [];
      }
    
      // === Najdi vechny pozice s 1 ===
      const allOnes = [];
      for (let i = 0; i < this.state.squares.length; i++) {
        for (let j = 0; j < this.state.squares[0].length; j++) {
          if (this.state.squares[i][j][0] === 1) {
            allOnes.push(i + "," + j);
          }
        }
      }
    
      // === Najdi smyku sloenou pouze z 1, kter pokrv vechny 1 ===
      let oneOnlyGroupThatCoversAll = null;
      for (const group of temp) {
        if (
          group.length > 0 &&
          group.every(
            cell => this.state.squares[cell.row][cell.col][0] === 1
          )
        ) {
          const covered = new Set(
            group.map(cell => cell.row + "," + cell.col)
          );
          if (allOnes.every(coord => covered.has(coord))) {
            oneOnlyGroupThatCoversAll = group;
            break;
          }
        }
      }
    
      if (oneOnlyGroupThatCoversAll) {
        console.log(" Pouze jedna smyka z 1 pokrv vechny jedniky.");
        // Najdi odpovdajc group (ze skupiny `groups`)
        const matchedIndex = groups.findIndex(
          g =>
            g.length === oneOnlyGroupThatCoversAll.length &&
            g.every(
              (cell, idx) =>
                cell.row === oneOnlyGroupThatCoversAll[idx].row &&
                cell.col === oneOnlyGroupThatCoversAll[idx].col
            )
        );
        const matchedGroup = matchedIndex !== -1
          ? groups[matchedIndex]
          : oneOnlyGroupThatCoversAll;
    
        this.Solution([oneOnlyGroupThatCoversAll], [matchedGroup]);
        this.drawGroup([oneOnlyGroupThatCoversAll], [matchedGroup]);
        return;
      }
    
      // === Pokud neexistuje ist smyka z 1, hledej nejvt pokrvajc smyku ===
      let maxGroup = [];
      for (const group of temp) {
        if (
          group.length > maxGroup.length &&
          group.some(cell => this.state.squares[cell.row][cell.col][0] === 1)
        ) {
          maxGroup = group;
        }
      }
    
      const coveredByMax = new Set(
        maxGroup
          .filter(cell => this.state.squares[cell.row][cell.col][0] === 1)
          .map(cell => cell.row + "," + cell.col)
      );
    
      if (allOnes.every(coord => coveredByMax.has(coord))) {
        console.log(" Nejvt smyka (s X) pokrv vechny jedniky.");
        const matchedIndex = groups.findIndex(
          g =>
            g.length === maxGroup.length &&
            g.every(
              (cell, idx) =>
                cell.row === maxGroup[idx].row &&
                cell.col === maxGroup[idx].col
            )
        );
        const matchedGroup = matchedIndex !== -1 ? groups[matchedIndex] : maxGroup;
        this.Solution([maxGroup], [matchedGroup]);
        this.drawGroup([maxGroup], [matchedGroup]);
        return;
      }
    
      // === Jinak zobraz vechny minimln skupiny ===
      this.Solution(temp, groups);
      this.drawGroup(temp, groups);
    }
    
    
  
    Solution(temp, groups) {                         // temp je pole ssouadnicemi sprvnch skupin
      const matrix = this.state.squares;           // hlavn matice
      var varNames = ["A", "B", "C", "D"];               // pole s nzvy promnnch matice
      var solution="";                              // etzec pro vpoet een jedn skupiny
      var vectorSol=[];                              // kad poloka je een jedn skupiny
      var k, j, t;
      // k je index pro prochzen pole varNames, j je index pro prochzen souadnic skupin, t je index pro prochzen binrnch souadnic
      var refRow, refCol;                    // tyto dv promnn obsahuj dek a sloupec prvnho prvku skupiny, kter je vchozm bodem
      var flag;                                    // pomocn promnn (sentinel)
      var coord;                                  // promnn, kter obsahuje binrn souadnici, se kterou se pracuje
      var elementsInRow;
      var solutionType=this.state.typeSol;
      for (let i = 0; i < temp.length; i++) {
  
        if (temp[i].length > 0) {
          k = 0;
          refRow = groups[i][0].row;              //extrakce souadnic referennho bodu kad skupiny
          refCol = groups[i][0].col;
  
          elementsInRow = 0;
          while (elementsInRow < groups[i].length && groups[i][elementsInRow].row === refRow)  //pot, kolik prvk je v jednom dku (vyuv se pi uren potu sloupc)
          {
            elementsInRow++;
          }
  
          //ZATEK KONTROLY DKU
          t = 0;
          coord = matrix[refRow][refCol][1];  // coord obsahuje binrn souadnici ve sloupci referennho bodu
          while (t < coord.length) {
            j = 1;
            flag = true;
            while (j < groups[i].length && groups[i][j].row === refRow) {       // dokud jsou prvky ve stejnm dku
              if (coord.charAt(t) !== matrix[refRow][groups[i][j].col][1].charAt(t)) {  // porovn jednotliv znaky binrnch souadnic ve sloupcch prvk skupiny
                flag = false;                                               // pokud nejsou stejn, promnn se nezohledn a cyklus se ukon
                break;
              }
              j++;
            }
            if (flag) {                        //een se aktualizuje pouze tehdy, pokud jsou vechny znaky stejn
              if(solutionType==="SOP")                //tvar SOP
              {
                if (coord.charAt(t) === "0") {
                  solution += "'" + varNames[k];
                }
                else{
                  solution += varNames[k];
                }
              }
              else{                               //tvar POS
                if (coord.charAt(t) === "0") {
                  solution += varNames[k];
                }
                else{
                  solution += "'" + varNames[k];
                }
                solution += "+";
              }
            }
            k++;
            t++;
          }
  
          //ZATEK KONTROLY DKU
          t = 0;
          coord = matrix[refRow][refCol][2];    // coord obsahuje binrn souadnici dku vchozho bodu
          while (t < coord.length) {
            j = elementsInRow;
            flag = true;
            while (j < groups[i].length && groups[i][j].col === refCol) {   // dokud jsou prvky ve stejnm sloupci
              if (coord.charAt(t) !== matrix[groups[i][j].row][refCol][2].charAt(t)) { // porovnv jednotliv znaky binrnch souadnic dk prvk nlecch do skupiny
                flag = false;                                     // pokud jsou znaky rzn, promnn se nepot a cyklus se peru
                break;
              }
              j += elementsInRow;
            }
            if (flag) {                        // een se aktualizuje pouze tehdy, pokud jsou vechny znaky stejn
              if(solutionType==="SOP")                 //tvar SOP
              {
                if (coord.charAt(t) === "0") {
                  solution +=  "'" + varNames[k];
                }
                else{
                  solution += varNames[k];
                }
              }
              else{                               //tvar POS
                if (coord.charAt(t) === "0") {
                  solution += varNames[k];
                }
                else{
                  solution += "'" + varNames[k];
                }
                solution += "+";
              }
            }
            k++;
            t++;
          }
          if(solutionType==="POS")     // v ppad tvaru POS bude na konci etzce znak "+" a ten je poteba odstranit
          {
            solution=solution.substr(0,solution.length-1);
          }
          vectorSol.push(solution);
          solution="";
        }
      }
  
      if (vectorSol[0] === "" || !vectorSol[0])   // pokud je een przdn etzec, znamen to, e matice obsahuje pouze sam 0 nebo sam 1
      {
        
        if (matrix[0][0][0] === 0) {
          vectorSol[0]="0";
        }
        else {
          vectorSol[0]="1";
        }
      }
      console.log("Expression sent to LogicCircuitGo:", vectorSol.join(" + ")); // DEBUG vstup
     // === Uloen minimalizovanho vrazu do state pro LogicCircuitGo
      this.setState({
      logicExpression: vectorSol.join(" + ")  // nap. ["A'B", "CD'"]  "A'B + CD'"
    }); 
      this.drawSolution(vectorSol);
    }
  
    drawGroup(temp, groups) {
      let color = ["red", "blue", "green", "orange", "#50C878","lightblue","#CD7F32","#ff6699"];  // pole barev
      let c = -1; // pouv se k identifikaci jednotlivch div, kter se pak budou mazat, zrove uruje barvu k pouit
      for (let i = 0; i < temp.length; i++) {  // cyklus, kter prochz vechny skupiny
        if (temp[i].length > 0 && groups[i].length !== Math.pow(2, this.state.typeMap)) {
          c++;
          let j = 0;
          let FirstElCol = groups[i][0].col;
          let FirstElRow = groups[i][0].row;
          while (j < groups[i].length) {                                    // cyklus, kter prochz vechny buky jedn skupiny
            let element = $("#" + groups[i][j].row + groups[i][j].col);    

            if (element.attr('class') && $("#" + element.attr('id') + c)) { // pokud u byl tento element vykreslen
              element.after("<div id=" + element.attr('id') + c + "></div>"); // vytvo se nov div hned po nm
              element = $("#" + groups[i][j].row + groups[i][j].col + c);    // a znovu se zsk
            }
            //console.log(!element.attr('class'))
            element.css("border-color", color[c]);                            // nastaven barvy okraje
            element.append("<div class='backgr' style='background-color: "+color[c]+"'></div>"); // pid se podkladov barva (div uvnit buky)

            // Vyhodnocen typu buky podle toho, kter dal buky v okol jsou soust stejn skupiny
            let right = this.checkElInGroups(j, groups[i], "right"); // doprava
            let below = this.checkElInGroups(j, groups[i], "below");   // dol
            let left = this.checkElInGroups(j, groups[i], "left"); // doleva
            let above = this.checkElInGroups(j, groups[i], "above"); // nahoru
  
          //  console.log("d: " + right + " sin: " + left + " above: " + above + " below: " + below);
            
          //vyhodnocen ppad pro piazen sprvn CSS tdy, kter uruje typ seskupen pro vykreslen dan buky
            if (right) {
              if (below) {
                if (left) {
                  if (groups[i][j].col === FirstElCol) element.addClass("TopLeft");
                  else if (j === ((groups[i].length / 2) - 1) || j === (groups[i].length - 1)) element.addClass("TopRig");
                  else element.addClass("top")
                }
                else if (above) {
                  if (j === groups[i].length - 2 || j === groups[i].length - 1) element.addClass("BotLeft");
                  else if (groups[i][j].row === FirstElRow) element.addClass("TopLeft");
                  else element.addClass("left");
                }
                else  element.addClass("TopLeft");
              }
              else if (above) {
                if (left) {
                  if (groups[i][j].col === FirstElCol) element.addClass("BotLeft");
                  else if (j === groups[i].length - 1 || j === (groups[i].length/2) - 1) element.addClass("BotRig");
                  else element.addClass("bot");
                }
                else element.addClass("BotLeft");
              }
              else if (left) {
                if (j === 0) element.addClass("ClosedLeft")
                else if (j === groups[i].length - 1) element.addClass("ClosedRig");
                else element.addClass("top-bot");
              }
              else element.addClass("ClosedLeft");
            }
  
            else if (above) {
              if (left) {
                if (below) {
                  if (groups[i][j].row === FirstElRow) element.addClass("TopRig");
                  else if (j === groups[i].length - 1 || j === groups[i].length - 2) element.addClass("BotRig");
                  else element.addClass("right");
                }
                else element.addClass("BotRig");
              }
              else if (below) {
                if (j === 0) element.addClass("ClosedTop");
                else if (j === groups[i].length - 1) element.addClass("ClosedBot");
                else element.addClass("left-right");
              }
              else element.addClass("ClosedBot");
            }
  
            else if (left) {
              if (below) element.addClass("TopRig");
              else element.addClass("ClosedRig");
            }
            else if (below) element.addClass("ClosedTop");
            else element.addClass("monoGroup");
            j++;
          }
        }
      }
    }
  
    checkElInGroups(j, groups, lato) {  // Vrac, ve kterm smru se nachz buky, kter pat do stejn skupiny jako zvolen buka (j je index buky ve skupin)
      const matrix = this.state.squares;
      let r = matrix[0].length;
      let c = matrix[0].length;
      if (this.state.typeMap === 3) {
        r = 2;
        c = 4;
      }
      // Cyklus, kter kontroluje, zda sousedn buky pat do stejn skupiny  podle toho nastavuje pznaky (flagy)
      for (let k = 0; k < groups.length; k++) {
        if (lato === "right" && (groups[k].col === (groups[j].col + 1) % c && groups[k].row === groups[j].row % r))
          return true; // kontrola buky vpravo
        if (lato === "below" && (groups[k].col === groups[j].col % c && groups[k].row === (groups[j].row + 1) % r))
          return true; // kontrola buky dole
        if (lato === "left") { // kontrola buky vlevo
          let col = groups[j].col - 1;
          if (col < 0) col = c - 1;
          if ((groups[k].col === col % c && groups[k].row === groups[j].row % r))
            return true;
        }
        if (lato === "above") { // kontrola buky nahoe
          let row = groups[j].row - 1;
          if (row < 0) row = r - 1;
          if ((groups[k].col === groups[j].col % c && groups[k].row === row % r))
            return true;
        }
      }
      return false;
    }
  
    drawSolution(vectorSol){   //metoda, kter zobraz vsledn een na obrazovku
      $(".Solution").show();
  
      let cost=0; 
      if(vectorSol[0]==="0" || vectorSol[0]==="1"){ //ppad, kdy je matice cel nulov nebo cel jednikov
        $("#sol").append("<div>"+ vectorSol[0]+ "</div>");
      }
      else{
        const typeSol = this.state.typeSol;
        let s = (typeSol==="SOP")? "+":"";   //vlo symbol podle zvolenho typu vrazu (SOP nebo POS)
        let cls = (typeSol==="SOP")? "groupSop":"groupPos"; //nastav CSS tdu podle typu vrazu

        //pole barev, odpovdajc barvm skupin, kter byly vykresleny  pro snadnou identifikaci
        let color = ["red", "blue", "green", "orange", "#50C878","lightblue","#CD7F32","#ff6699"];  
  
        for(let i=0; i<vectorSol.length; i++){ //prochz cel een, rozdlen po skupinch
           //vytvo div se sprvnou barvou, odpovdajc barv skupiny
          $("#sol").append("<div id='sol"+i+"' class='"+cls+"' style='background-color: "+color[i]+"'></div>");
          
          for(let j=0; j<vectorSol[i].length; j++){ //prochz jednotliv znaky jednoho logickho vrazu skupiny

            if(vectorSol[i][j]!=="'")
              $("#sol"+i).append(vectorSol[i][j]+" "); //pokud nen apostrof, jednodue pid znak
            else{
              //pokud je apostrof, neguje nsledujc promnnou  pid ji s rou nahoe
              $("#sol"+i).append("<span style='text-decoration: overline'>"+vectorSol[i][++j]+"</span> ");
            }
            if(vectorSol[i][j]!=="+") cost++;
          }
          if(i!==vectorSol.length-1) $("#sol").append("<div class='plus'> "+s+" </div>"); // pid zvolen symbol (na zatku metody) podle vybranho typu een (SOP nebo POS)
        }
      }
      

      // umst een piblin na sted pod mapu, aby se pi zvyovn potu prvk elementsInRowozbil rozvren strnky
      $(".Solution").css("left", parseInt($(".Solution").css("left"))-parseInt($(".Solution").css("width"))/2);
    }
  
    render() {
      // konstanty uchovvajc hodnoty stavovch promnnch
      const values = this.state.squares;
      const typeMap = this.state.typeMap;
      const perm = this.state.perm;
      const typeSol = this.state.typeSol;
      // pokud se nepouije ()=> u tlatka, pi kadm kliknut na tverce se znovu zavol funkce
      let i = 0; //pouv se jako key
      return (
        <div key={i++}>
            <div className="title"><h1> Minimalizace Karnaughovy mapy </h1></div>
          <div className="bodyPage" key={i++}>
            <p className="nameTab"> Pravdivostn tabulka </p>
            <div className="truthTable" key={i++}>
              <TruthTable
                squares={values}
                typeMap={typeMap}
                perm={perm}
                key={i++}
                onClick={(i, j) => this.handleClick(i, j)}
                setRowOrColCell={(i, j, k, val) => this.setRowOrColCell(i, j, k, val)}
              />
            </div>
            <div className="kMap">
              <Map
                squares={values}
                typeMap={typeMap}
                onClick={(i, j) => this.handleClick(i, j)}
              />
            </div>
  
            <OptionButton
              squares={values}
              typeMap={typeMap}
              typeSol={typeSol}
              onClick={() => this.Algorithm(values)}
              setTypeSol={(val) => this.setTypeSol(val)}
              setMatrixSquare={(val) => this.setMatrixSquare(val)}
              setTypeMap={(val) => this.setTypeMap(val)}
            />
            <div className="Solution">
              <div>{typeSol} tvar:</div>
              <div className="sol" id="sol">  
              </div>
              <div id="cost"> 
                </div>
                <LogicCircuitGo
                  expression={this.state.logicExpression}
                  type={this.state.typeSol}
              />  
            </div> 
          </div>
        </div>
      );
    }
  }
  
  function isPower(x, y) {
    if (x === 1)
      return (y === 1);
  
    var pow = 1;
    while (pow < y)
      pow *= x;
  
    return (pow === y);
  }   